<template>
  <div class="tem-nav">
    <img src="./images/logo.png" alt="logo">
    <ul class="tem-nav-menu">
      <li v-for="item in navMenuUrlList"><a :href="item.url">{{item.title}}</a></li>
    </ul>
  </div>
</template>
<script>
  export default{
    data(){
      return {
        navMenuUrlList:[
          {title:'首页',url:'/module/index.html'},
          {title:'我要投资'},
          {title:'我要贷款'},
          {title:'个人中心',url:'/module/register.html'},
          {title:'新手指引',url:'/module/login.html'},
          {title:'关于我们'}
        ]
      }
    },
    methods: {}
  }
</script>

<style>
  .tem-nav{height: 80px;display: flex;align-items: center;padding: 0 100px;}
  .tem-nav a{color: #777;}
  .tem-nav-menu{display: flex;}
  .tem-nav-menu li{min-width: 80px;text-align: center;height: 78px;line-height: 80px;}
  .tem-nav-menu li:hover{background-color: #ccc;}
</style>


