<template>
  <div class="information-item">
    <div class="information-item-header">
      <span class="information-item-title"><a href="">{{title}}</a></span>
      <span class="information-item-more"><a href="">更多资讯 ></a></span>
    </div>
    <ul class="information-item-list">
      <li v-for="item in dataList">
        <span><a href="">{{item.title}}</a></span>
        <span class="information-item-date">发表日期:{{date | GetNowDateTime}}</span>
      </li>
    </ul>
  </div>
</template>

<script>
  export default{
    data(){
      return {
        date: new Date()
       }
    },
    props: ['title', 'dataList'],
    methods: {},
    filters: {
      GetNowDateTime: val => {
        let nowTime = val;
        let NowDateTime = nowTime.getFullYear() + "年" +
            (nowTime.getMonth() + 1 ) + "月" +
            nowTime.getDate() + "日" +
            " ";

        return NowDateTime;
      }
    },
  }
</script>

<style>
  .information-item {
    width: 50%;
    padding-bottom: 40px;
  }

  .information-item-header {
    display: flex;
    justify-content: space-between;
  }

  .information-item-title {
    font-size: 24px;
    color: #2e8ece;
    font-weight: bold;
    margin-bottom: 20px;
    border-bottom: 2px solid #2e8ece;
    padding-bottom: 4px;
  }

  .information-item-more {
    color: #337ab7;
    padding: 15px 20px;
    font-size: 12px;
  }

  a {
    color: #2e8ece;
  }

  .information-item-list a {
    color: #555;
    line-height: 38px;
    font-size: 14px;
  }
  .information-item-list a:hover{
    color: #2e8ece;
  }

  .information-item-list li {
    display: flex;
    justify-content: space-between;
    color: #555;
    line-height: 38px;
    font-size: 14px;
  }
  .information-item-date{padding-right: 10%;}
</style>


