<template>
  <div class="tem-header">
    <div class="tem-header-tel">
      <img src="./images/tel.png" alt="tel">
      <a href="">123-4567890</a>
    </div>
    <ul>
      <li v-for="item in menuList"><a :href="item.menuUrl">{{item.menuItem}}</a></li>
    </ul>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        menuList:[{menuItem: '首页',menuUrl:'/module/index.html'}, {menuItem: '小强'}, {menuItem: '赶快冲值'}, {menuItem: '注销'}, {menuItem: '帮助'}, {menuItem: '联系客服'}]
      }
    }
  }
</script>
<style>
  .tem-header {
    height: 50px;
    display: flex;
    justify-content: space-between;
    background-color: #333;
    padding: 0 100px;
  }

  .tem-header-tel {
    display: flex;
    align-items: center;
  }

  .tem-header-tel a {
    font-size: 24px;
    font-style: italic;
    color: #fff;
  }

  .tem-header-tel img {
    height: 30px;
  }

  .tem-header ul {
    display: flex;
    align-items: center;
  }

  .tem-header ul a {
    font-size: 14px;
    color: #fff;
  }

  .tem-header ul a:hover {
    color: #337ab7;
  }

  .tem-header ul li {
    padding-left: 30px;
  }
</style>



