import Vue from 'vue'
import App from './App'
import resource from 'vue-resource'
import element from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
Vue.use(resource)
Vue.use(element)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  components: { App }
})



