<template>
  <div class="tem-borrowing">
    <span v-text="borrowingTitle" class="borrowing-title"></span>
    <v-borrowing-item></v-borrowing-item>
  </div>
</template>

<script>
  import borrowingItem from './borrowing-item/borrowing-item.vue'
  export default{
    data(){
      return {
        borrowingTitle: '进行中借款'
      }
    },
    methods: {},
    components: {
      'v-borrowing-item': borrowingItem
    }
  }
</script>

<style>
  .tem-borrowing {
    padding: 10px 100px;
  }

  .borrowing-title {
    font-size: 24px;
    color: #2e8ece;
    font-weight: bold;
    margin-bottom: 20px;
    border-bottom: 2px solid #2e8ece;
    display: block;
    width: 120px;
    padding-bottom: 4px;
  }
</style>


